* {
  box-sizing: border-box;
  user-select: none;
  transition: 0.5s;
  font-family: 'Barlow' !important;
  font-weight: bold;
  list-style: none;
  transition: 0.5s;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #282c34;
}

@font-face {
  font-family: 'Barlow';
  font-weight: normal;
  src: url('https://chcblogs.com/fonts/Barlow-Regular.woff2') format('woff2'), url('https://chcblogs.com/fonts/Barlow-Regular.ttf') format('truetype');
}
@font-face {
  font-family: 'Barlow';
  font-weight: medium;
  src: url('https://chcblogs.com/fonts/Barlow-Medium.woff2') format('woff2'), url('https://chcblogs.com/fonts/Barlow-Medium.ttf') format('truetype');
}
@font-face {
  font-family: 'Barlow';
  font-weight: 600;
  src: url('https://chcblogs.com/fonts/Barlow-SemiBold.woff2') format('woff2'), url('https://chcblogs.com/fonts/Barlow-SemiBold.ttf') format('truetype');
}
@font-face {
  font-family: 'Barlow';
  font-weight: bold;
  src: url('https://chcblogs.com/fonts/Barlow-Bold.woff2') format('woff2'), url('https://chcblogs.com/fonts/Barlow-Bold.ttf') format('truetype');
}
@font-face {
  font-family: 'Barlow';
  font-weight: 800;
  src: url('https://chcblogs.com/fonts/Barlow-ExtraBold.woff2') format('woff2'), url('https://chcblogs.com/fonts/Barlow-ExtraBold.ttf') format('truetype');
}
@font-face {
  font-family: 'Barlow';
  font-weight: 900;
  src: url('https://chcblogs.com/fonts/Barlow-Black.woff2') format('woff2'), url('https://chcblogs.com/fonts/Barlow-Black.ttf') format('truetype');
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: #007acc;
  border-radius: 10px;
}

@media screen and (max-width: 450px) {
  html {
    width: 100%;
  }
}
