header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  height: 65px;
  background-color: #5f6368;
  color: white;
  z-index: 999;
}

.lazyload-wrapper {
  margin-top: 90px;
}

.lazyload-wrapper .imgbox:hover {
  box-shadow: 0 1px 2px -2px #d6d9db, 0 3px 6px 0 #d6d9db, 0 5px 12px 4px #d6d9db;
}

button {
  width: 75px;
  height: 35px;
  border-radius: 2px;
  background-color: #1890ff;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
}

button:hover {
  border: 2px solid rgba(255, 255, 255, 0.6);
}

select {
  width: auto;
  height: 35px;
  padding: 3px;
  border-radius: 2px;
  border: none;
  outline: none;
  background-image: -moz-linear-gradient(90deg, rgb(158, 182, 255), rgb(204, 255, 222));
  background-image: -webkit-linear-gradient(90deg, rgb(158, 182, 255), rgb(204, 255, 222));
  background-image: linear-gradient(90deg, rgb(158, 182, 255), rgb(204, 255, 222));
}
